// import {Raphael} from './rSu'

// 
declare var Raphael: any
declare var $:any

interface numberMap {
    [k: number]: any
}



// 
var instanceIndex = 0
var instanceSource: numberMap = {}     // 

// ()
class H{
    /**
     * 
     * @param {*} config 
     */
    static createInstance(config?: any){
        config = 'object' == typeof config? config:{}
        if(!config.dom){
            if(process.env.NODE_ENV !== 'production'){
                console.warn('[Worker]  config.dom')
            }
        }
        //  HTML
        if('string' == typeof config.dom){
            config.dom = $(config.dom)
        }
        if(!config.w){
            // config.w = parseInt(width * 1.1)
            config.w = $(window).width() * 1.1
        }
        if(!config.h){
            config.h = $(window).height() * 1.1
        }
        return Raphael(config.dom.get(0), config.w, config.h)
    }
    static onMoveEvt(){}
    static onStartEvt(){}
    static onEndEvt(){}
    /**
     * 
     */
    static getIndex(){
        instanceIndex += 1
        return instanceIndex
    }
    /**
     * 
     * @param {number} index 
     * @param {string|null} key 
     * @param {*} value 
     */
    static src(index: number, key?:any, value?: any){
        if(!instanceSource[index]){
            instanceSource[index] = {}
        }
        var dd = instanceSource[index]
        if('undefined' == typeof key){
            return dd
        }
        if('undefined' == typeof value){
            return dd[key] || null
        }
        dd[key] = value
    }
}

export default H


