import NodeAbstract from "./NodeAbstract";
export default class NodeAudit extends NodeAbstract{
    xRate: number           // 
    protected _onInit(){
        this.NodeType = 'audit'
        this.xRate = 0.20
    }
    protected _whenCreatorEvt(){
        this.opt.bkg = this.opt.bkg || '#88EEEA'
        var pQue = this.opt2Attr(),
            nOpt = this.opt            

        this.c = this.paper.path(this._ps2Path(pQue, true))
        this.c.attr('fill', nOpt.bkg)
        // 
        if(nOpt.text){
            let {x, y} = this._getTextPnt()
            this.label = this.paper.text(x, y, nOpt.text)
        }
    }
    /**
     * 
     * @param {obejct|null} opt  
     * @returns {array} 
     */
    opt2Attr(opt?: rSu.NodeOpt): rSu.P[]{
        var nopt = opt? opt: this.opt,
            cx = nopt.cx,
            cy = nopt.cy,
            w = nopt.w,
            h = nopt.h,
            xRate = this.xRate

        return [
            {   // A
                x: cx - w/2,
                y: cy - h/2
            },
            {   // B
                x: (cx + w/2) + (w * xRate),
                y: cy - h/2
            },
            {   // C
                x: cx + w/2,
                y: cy + h/2
            },
            {   // D
                x: (cx - w/2) - (w * xRate),
                y: cy + h/2
            }
        ]
    }
    /**
     * 
     * @param nOpt 
     */
    updAttr(nOpt: rSu.NodeOpt): rSu.Node{
        this._updAttr(nOpt)
        var opt = this.opt2Attr()
        this.c.attr('path', this._ps2PathAttr(opt, true))
        this.updTextAttr(nOpt.text)  //         
        this.onSize()
        return <rSu.Node>this
    }
}


