import { NodeQue } from "./NodeQue";
import ObjX from "./ObjX";
import { Util } from "./util";
import { cNode } from "./confNode";

/**
 * 2018329 
 * 
 */

export default class ToolBar{
    private paper: RaphaelPaper
    option: rSu.bsMap       // 
    config: rSu.bsMap       //  {option: config}
    private ndMer: rSu.NodeQue
    headElems: rSu.MapRElm  // 
    nodeElems: rSu.MapRElm  // 
    connElems: rSu.MapRElm  // 
    rData: {      // 
        [k: string]: any
        cp: rSu.P                // 
        th0: number,             // 
        th1: number,             // 
        th2: number,             // 
        nh: number,              // 
        ch: number,              // 
        cw: number               // 
    }
    // title node 
    // 
    tBodyNds: rSu.mapNode

    // 
    // 
    cBodyNds: rSu.mapNode

    protected _tools: RaphaelElement[] = []     // 
    constructor(paper: RaphaelPaper, opt?: rSu.bsMap){
        this.rData = {
            cp: {x: 5, y: 5},
            th0: 23,            // 
            th1: 23,            // 
            th2: 23,            // 
            nh: 0,              // 
            ch: 0,              // 
            cw: 75              // 
        }
        this.paper = paper
        this.ndMer = new NodeQue(this.paper)
        this.option = opt
        this.config = opt.toolBar || {}
        this.config.aUpSrc = ObjX.value(this.config, 'aUpSrc', 'arrow_up.png')
        this.config.aDownSrc = ObjX.value(this.config, 'aDownSrc', 'arrow_down.png')
        this._headBar()
        this._nodeBar()
        this._connBar()
    }
    /**
     * 
     */
    private _headBar(){
        var $this = this,
            {cp, cw, th0} = this.rData,
            {x, y} = cp,
            paper = this.paper,
            ist: RaphaelElement
        
        this.headElems = {}

        ist = paper.rect(x, y, cw, th0)
            .attr('fill', '#ffffff')
            .click(function(){
                let toggle = this.data('toggle')
                if(toggle != 'H'){
                    this.data('toggle', 'H')
                    $this.toggle('H')
                }else{
                    this.data('toggle', 'S')
                    $this.toggle('S')
                }
            })
        this.headElems['icon'] = ist
        
        ist = paper.text(x + (cw/2), y+10, ObjX.value(this.config, 'title', ''))
        this.headElems['title'] = ist
    }
    /**
     * 
     */
    private _nodeBar(){
        var $this = this,
            {cp, cw, th1, nh} = this.rData,
            {x, y} = cp,
            {paper, ndMer, config} = this,
            ist: rSu.Node,
            tBodyNds: rSu.mapNode = {}         // 
            
        
        this.nodeElems = {}
        let menuSeting: any = config.menu || false          // 
        if(!menuSeting){        // 
            menuSeting = [
                'begin', 'task', 'sign', 'cond', 
                'subFlow', 'parallel', 'merge', 'end',
                'text'
            ]
            ;
        }

        y += th1
        // data: toggle => H/S
        this.nodeElems['title'] = paper.rect(x, y, cw, 23)
            .attr('fill', '#ffffff')
            .click(function(){
                // console.log(this)
                let toggle = this.data('toggle')
                let iconIst = $this.nodeElems['icon']
                if(toggle != 'H'){
                    this.data('toggle', 'H')
                    if(iconIst){
                        iconIst.attr('src', config.aDownSrc)
                    }
                    $this.tToggle('H')
                }else{
                    this.data('toggle', 'S')
                    if(iconIst){
                        iconIst.attr('src', config.aUpSrc)
                    }
                    $this.tToggle('S')
                }
            })
        this.nodeElems['icon'] = paper.image(config.aUpSrc, x+cw*0.7, y+1, 20, 20)

        nh = y        
        y += 23
        this.nodeElems['tBody'] = paper.rect(x, y, cw, 250)
            .attr('fill', '#ffffff')
        
        // 
        x += 32
        Util.each(menuSeting, (mk: string|number, row: any) => {
            if('object' != typeof row){
                mk = row
                row = {}
            }
            if(!cNode[mk]){
                return
            }
            // console.log(mk)
            y += 32
            let text = row.text || cNode[mk].text,
                cx = x,
                cy = y
            
            // 
            if('parallel' == mk){
                cy += 5
                cx += 20
            }
            else if('merge' == mk){
                cy += 5
                cx += 20
            }

            ist = ndMer.make(<string>mk, {cx, cy, w: 40, h: 20, text: text})
                .creator()
            if(ist.label){
                ist.label.attr('fill', '#FF8C00')
                // ist.label.attr('fill', '#FFA500')
                
            }
            // 
            if('text' == mk){
                ist.c.attr({
                    'font-size': 15,
                    'stroke': 'none'
                })
            }

            tBodyNds[<string>mk] = ist
        })

        this.tBodyNds = tBodyNds
        this.rData.nh = y - nh
        this.nodeElems['tBody'].attr('height', this.rData.nh)
    }
    /**
     * 
     */
    private _connBar(){
        var $this = this,
            {cp, cw, th2, ch, th0, th1, nh} = this.rData,
            {x, y} = cp,
            {paper, ndMer, config} = this,
            ist: rSu.Node,
            cBodyNds: rSu.mapNode = {}         // 
            
        this.connElems = {}
        // 
        // data: toggle => H/S
        y += th0 + th1 + nh

        this.connElems['title'] = paper.rect(x, y, cw, th2)
            .attr('fill', '#ffffff')
            .click(function(){
                // console.log(this)
                let toggle = this.data('toggle')
                let iconIst = $this.connElems['icon']
                if(toggle != 'H'){
                    this.data('toggle', 'H')
                    if(iconIst){
                        iconIst.attr('src', config.aDownSrc)
                    }
                    $this.cToggle('H')
                }else{
                    this.data('toggle', 'S')
                    if(iconIst){
                        iconIst.attr('src', config.aUpSrc)
                    }
                    $this.cToggle('S')
                }
            })
        this.connElems['icon'] = paper.image(config.aUpSrc, x+cw*0.7, y+1, 20, 20)
        
        // y += 23
        ch = y
        y += th2
        let prevH = 60  // 
        this.config['lnSeledBkg'] = this.config['lnSeledBkg'] || '#CCFF99'
        this.config['lnDefBkg'] = this.config['lnDefBkg'] || '#ffffff'
        let {lnDefBkg} = this.config

        this.connElems['lnCon'] = paper.rect(x, y, cw, prevH/2)
            .attr('fill', lnDefBkg)
            // .attr(conAttr)
        let ly = y + prevH/4*0.7,
            lx = x + 20
        ist = ndMer.make('ln', {
            P1: {x: lx-5, y: ly},
            P2: {x: lx+25, y: ly}
        })
            .creator()
        cBodyNds.ln = ist
        

        // 
        y += 20
        this.connElems['lnPolyCon'] = paper.rect(x, y, cw, prevH/2)
            .attr('fill', lnDefBkg)
            // .attr(conAttr)
        ly = y + prevH/4*0.7,
            lx = x + 20
        ist = ndMer.make('lnPoly', {
            P1: {x: lx-5, y: ly},            
            P2: {x: lx+20, y: ly + 4},
            h: 4
        })
            .creator()
        cBodyNds.lnPoly = ist

        this.cBodyNds = cBodyNds
        ch = y - ch
        this.connElems['lnCon'].attr('height', ch/2)
        this.connElems['lnPolyCon'].attr('height', ch/2)
    }
    /**
     * 
     */
    connSizeNode(backMk?: boolean){
        var {title, icon, lnCon, lnPolyCon} = this.connElems,
            {cp, cw, th2, ch, th0, th1, nh} = this.rData,
            {x, y} = cp,
            {ln, lnPoly} = this.cBodyNds

        y += backMk? th0 + th1 + nh: th0 + th1
        title.attr('y', y)
        icon.attr('y', y+1)
        
        y += th2
        let prevH = 60  // 
        lnCon.attr('y', y)

        // 
        let ly = y + prevH/4*0.7,
            lx = x + 20
        ln.updAttr({
            P1: {x: lx-5, y: ly},
            P2: {x: lx+25, y: ly}
        })

        // 
        y += 20
        lnPolyCon.attr('y', y)
        ly = y + prevH/4*0.7,
            lx = x + 20
        lnPoly.updAttr({
            P1: {x: lx-5, y: ly},            
            P2: {x: lx+20, y: ly + 4},
            h: 4
        })
    }

    /**
     * 
     * @param {string} type  H/S
     * @param {boolean} includeTit 
     */
    tToggle(type?:string, includeTit?: boolean){
        var tBodyNds = this.tBodyNds,
            nodeElems = this.nodeElems,
            {tBody} = nodeElems
        if('H' != type){
            Util.each(tBodyNds, (k: number, nd: rSu.Node) => {
                nd.show()
            })
            if(includeTit){
                nodeElems.title.show()
                nodeElems.icon.show()
            }
            if(tBody){
                tBody.show()
            }
            this.connSizeNode(true)
        }
        else{
            Util.each(tBodyNds, (k: number, nd: rSu.Node) => {
                nd.hide()
            })
            if(includeTit){
                nodeElems.title.hide()
                nodeElems.icon.hide()
            }
            if(tBody){
                tBody.hide()
            }
            this.connSizeNode()
        }
    }
    /**
     * 
     * @param {string} type  H/S
     */
    cToggle(type?:string, includeTit?: boolean){
        var cBodyNds = this.cBodyNds,
            connElems = this.connElems,
            {lnCon, lnPolyCon} = connElems
        if('H' != type){
            Util.each(cBodyNds, (k: number, nd: rSu.Node) => {
                nd.show()
            })
            if(includeTit){
                connElems.title.show()
                connElems.icon.show()
            }
            if(lnCon){
                lnCon.show()
            }
            if(lnPolyCon){
                lnPolyCon.show()
            }
        }
        else{
            Util.each(cBodyNds, (k: number, nd: rSu.Node) => {
                nd.hide()
            })
            if(includeTit){
                connElems.title.hide()
                connElems.icon.hide()
            }
            if(lnCon){
                lnCon.hide()
            }
            if(lnPolyCon){
                lnPolyCon.hide()
            }
        }
    }
    /**
     * 
     * @param {string} type 
     */
    toggle(type?: string){
        this.tToggle(type, true)
        this.cToggle(type, true)
    }
    /**
     * 
     */
    show(){
        this.toggle('S')
        this.cToggle('S')
        Util.each(this.headElems, (k: string, elem: RaphaelElement) => {
            elem.show()
        })
    }
    /**
     * 
     */
    hide(){
        this.toggle('H')
        this.cToggle('H')

        Util.each(this.headElems, (k: string, elem: RaphaelElement) => {
            elem.hide()
        })
    }
}


