///<reference path="../../index.d.ts"/>
///<reference path="../types/raphael.ts"/>
///<reference path="../types/jquery.ts"/>

// declare var $: jQuery
// declare var $: jquery
    // BUG[180331]  <reference path="../types/jquery.ts"/> 
declare var $: any
import { Util } from "../util"
import {cNode} from '../confNode'
import NodeUtil from "./NodeUtil";


/**
 * @export
 * @abstract
 * @class NodeAbstract
 */
export default abstract class NodeAbstract{
    conLns: {
        from?: string[]    // [code: string] 
        to?: string[]
    }
    NodeType: string            // 
    opt: rSu.NodeOpt            // 
    label?: RaphaelElement      // 
    c?: RaphaelElement          // 
    paper: RaphaelPaper         // Raphael 
    isSelEd: boolean            // 

    // 
    xRate?: number           // 
    inlinesEle?: RaphaelElement[]    // 
    inlineEle?: RaphaelElement // 
    tRElem: rSu.MapRElm        // 
    protected _dataQueueDick: rSu.bsMap
    protected _code: string

    constructor(paper: RaphaelPaper, opt?: rSu.NodeOpt){
        this.conLns = {
            from: [],
            to: []
        }
        this.tRElem = {}
        this._dataQueueDick = {}
        this.isSelEd = false
        this.paper = paper
        this.NodeType = null            // 
        // 
        if(opt){
            opt.bkgMagnetic = opt.bkgMagnetic || '#FF0000'
            let features = opt.features || {}
            this.opt = opt
        }        
        this._onInit()
    }    
    /**
     * 
     * @param {string|object} key 
     * @param {*} value 
     * @param {*} def  
     */
    feature(key: string|rSu.bsMap, value?: any, def?: any): rSu.Node|any{
        let feature = this.opt.features || {}
        if(!value){
            if('object' == typeof key){
                return null
            }
            let gValue = feature[key] || null
            if(def && !gValue){
                feature[key] = def
                this.opt.features = feature
                return def
            }
            return gValue
        }else{
            if('object' == typeof key){
                feature = Util.jsonMerge(feature, key)
            }
            else{
                feature[key] = value
            }
            this.opt.features = feature
            return <rSu.Node>this
        }
    }
    /**
     * @param {string|number|object} key _code 
     * @param {*} value 
     */
    data(key?:any, value?:any): any{        
        if('undefined' == typeof value){
            if('undefined' == typeof key){
                return Util.clone(this._dataQueueDick)
            }
            else if('object' == typeof key){
                Util.each(key, (k: any, v: any) => {
                    this._dataQueueDick[k] = v
                })
                return this
            }
            return this._dataQueueDick[key]
        }else{
            this._dataQueueDick[key] = value
            //  code 
            if(!this._code && key == '_code'){
                this._code = value
            }
        }
        return this
    }
    /**
     * 
     */
    get code():string{
        return this._code
    }
    /**
     *  name 
     * @readonly
     * @type {string}
     * @memberof NodeAbstract
     */
    get name(): string{
        let txt = this.opt.text || ''
        if(txt && txt.indexOf('\n') > -1){
            txt = txt.replace(/\n/g, '')
        }
        return txt
    }
    /**
     *  { cNode } = confNode 
     * @readonly
     * @type {string}
     * @memberof NodeAbstract
     */
    get _key(): string{
        let nt = this.NodeType
        if(nt.indexOf('_') > -1){
            let aStr = nt.split('_')
            Util.each(aStr, (idx: number, v: string) => {
                if(idx > 0){
                    v = v.substr(0, 1).toLocaleUpperCase() + v.substr(1)
                    aStr[idx] = v
                }
            })
            nt = aStr.join('')
        }
        return nt
    }
    /**
     * 
     * @readonly
     * @type {number}
     * @memberof NodeAbstract
     */
    get type(): number{
        let code = this.code,
            attr = cNode[this._key],
            tp: number = attr? attr.type : null
        return tp
    }
    /**
     * 
     * @param opt 
     */
    creator(opt?: rSu.NodeOpt){
        this._whenCreatorEvt()
        return this
    }
    /**
     * path
     * @param {array} pQue 
     * @param {bool} isClose 
     * @returns {string}
     */
    protected _ps2Path(pQue: rSu.P[], isClose?: boolean): string{
        return NodeUtil.ps2Path(pQue, isClose)
    }
    /**
     * 
     * @param {array} pQue 
     * @param {bool} isClose 
     * @returns {string}
     */
    protected _ps2PathAttr(pQue: rSu.P[], isClose?: boolean){
        return NodeUtil.ps2PathAttr(pQue, isClose)
    }
    /**
     * ()
     * @param value 
     * @param isEnd 
     */
    line(value: string, isEnd?: boolean): rSu.Node{
        if(isEnd){
            this.conLns.to.push(value)
        }
        else{
            this.conLns.from.push(value)
        }
        return <rSu.Node>this
    }
    /**
     * 
     * @param type 
     * @param code 
     */
    rmLine(value: string, isEnd?: boolean): rSu.Node{
        if(value){
            if(isEnd){
                let tLns: string[] = []
                Util.each(this.conLns.to, (k: number, code: string) => {
                    if(code != value){
                        tLns.push(code)
                    }
                })
                this.conLns.to = tLns
            }
            else{
                let fLns: string[] = []
                Util.each(this.conLns.from, (k: number, code: string) => {
                    if(code != value){
                        fLns.push(code)
                    }
                })
                this.conLns.from = fLns
            }
        }
        return <rSu.Node>this
    }
    /**
     * 
     */
    protected _whenCreatorEvt(){}
    /**
     *  []
     */
    protected _onInit(){}
    /**
     *  []
     */
    onDrag(){}
    /**
     * 
     */
    getPLen(P1: rSu.P, P2: rSu.P): number{
        return Math.pow(
            (Math.pow((P1.x - P2.x), 2) + Math.pow((P1.y - P2.y), 2)),
            1/2
        )
    }
    /**
     * 
     * @param nOpt 
     */
    protected _updAttr(nOpt: rSu.NodeOpt){
        for(var key in nOpt){
            if('undefined' != typeof this.opt[key]){
                this.opt[key] = nOpt[key]
            }
        }
        return this
    }
    /**
     * 
     */
    protected _getTextPnt(): rSu.P{
        let {cx, cy} = this.opt,
            p = {x: cx + 1, y: cy + 1}
        return p
    }
    /**
     * 
     */
    delete(){        
        this.c.remove()
        if(this.label){
            this.label.remove()
        }
        if(this.inlineEle){
            this.inlineEle.remove()
        }
        if(this.inlinesEle){
            this.inlinesEle.forEach((ist: RaphaelElement) => {
                ist.remove()
            })
        }
        this.clearTmpElem()
    }
    /**
     * 
     */
    hide(){
        if(this.c){
            this.c.hide()
        }
        if(this.label){
            this.label.hide()
        }
        // 
        if(this.inlinesEle){
            this.inlinesEle.forEach((nd) => {
                nd.hide()
            })
        }
        if(this.inlineEle){
            this.inlineEle.hide()
        }
        return this
    }
    /**
     * 
     */
    show(){
        if(this.c){
            this.c.show()
        }
        if(this.label){
            this.label.show()
        }
        // 
        if(this.inlinesEle){
            this.inlinesEle.forEach((nd) => {
                nd.show()
            })
        }
        if(this.inlineEle){
            this.inlineEle.show()
        }
        return this
    }
    /**
     * 
     * data => {afterUpd(x, y, $node), beforeMv($node)}
     * @returns 
     * @memberof NodeAbstract
     */
    moveable(data?: rSu.bsMap): rSu.Node{
        var $this = this;
        data = 'object' == typeof data? data : {}
        this.c.undrag()
        var tP = {cx: 0, cy: 0}
        this.c.drag(
            function(dx: number, dy: number){
                if(data.beforeMv && 'function' == typeof data.beforeMv){
                    // 
                    if(false === data.beforeMv($this)){
                        return
                    }
                }

                dx += tP.cx
                dy += tP.cy
                $this.updAttr({cx: dx, cy: dy})
                $this.select()
                if(data.afterUpd && 'function' == typeof data.afterUpd){
                    data.afterUpd(dx, dy, $this)
                }
            },
            function(){
                let {cx, cy} = $this.opt
                tP = {cx, cy}
            }
        )
        return <rSu.Node>$this
    }
    /**
     * 
     * @param nOpt 
     */
    updAttr(nOpt: rSu.NodeOpt): rSu.Node{
        return <rSu.Node>this
    }    
    /**
     * 
     * 
     * @param {string} [text] 
     * @memberof NodeAbstract
     */
    updTextAttr(text?: string): rSu.Node{
        let {x, y} = this._getTextPnt()
        if(this.label){
            this.label.attr({
                x, y
            })
        }
        // 
        if(text){
            if(this.label){
                this.label.attr('text', text)
            }else{
                this.label = this.paper.text(x, y, text)
            }
        }
        return <rSu.Node>this
    }
    /**
     * 
     */
    getBBox(): rSu.BoxAttr{
        let {x, y, width, height} = this.c.getBBox(),
            boxPadding = this.feature('boxPadding', null, 3)
        x -= boxPadding, y -= boxPadding
        width += boxPadding*2, height += boxPadding*2

        //  
        let mx = x + width/2, 
            xx = x + width,
            my = y + height/2,
            xy = y + height,
            ps: rSu.bsMap = {
                a: {x, y},             // A
                b: {x: mx, y},         // B
                c: {x: xx, y},         // C
                d: {x: xx, y: my},     // D
                e: {x: xx, y: xy},     // E
                f: {x: mx, y: xy},     // F
                g: {x: x, y: xy},      // G
                h: {x: x, y: my}       // H
            }
        let attr = {x, y, width, height}
        return {attr, ps}
    }
    /**
     *  icon 
     */
    getIconP(): rSu.P{
        let p: rSu.P = this._getTextPnt(),
            {w, h} = this.opt
        p.x -= w/2 - 5
        p.y -= h/2 - 5
        return p
    }
    /**
     * 
     * @param px
     * @param py
     */
    magnCore(px: number, py: number): RaphaelElement{
        let bAttr = this.getBBox(),
            {attr, ps} = bAttr,
            {x, y} = attr,
            w = attr.width,
            h = attr.height
        // a-h
        let pt: rSu.P,
            cx1 = x + w/4,
            cx = x + w/2,
            cx2 = x + w*(3/4),
            cy1 = y + w/4,
            cy = y + w/2,
            cy2 = y + w*(3/4),
            posi = null

        if(px <= cx1 && py <= cy1){
            pt = ps.a
            posi = 'a'
        }
        else if((px > cx1 && px < cx2) && py <= cy1){
            pt = ps.b
            posi = 'b'
        }
        else if((px >= cx2) && py <= cy1){
            pt = ps.c
            posi = 'c'
        }
        else if((px >= cx2) && (py > cy1 && py < cy2)){
            pt = ps.d
            posi = 'd'
        }
        else if((px >= cx2) && py >= cy2){
            pt = ps.e
            posi = 'e'
        }
        else if((px > cx1 && px < cx2) && py >= cy2){
            pt = ps.f
            posi = 'f'
        }
        else if((px <= cx1) && py >= cy2){
            pt = ps.g
            posi = 'g'
        }
        else if((px<= cx1) && (py > cy1 && py < cy2)){
            pt = ps.h
            posi = 'h'
        }
        this.clearTmpElem('mc')
        if(pt){
            this.tRElem['mc'] = this.paper
                .circle(pt.x, pt.y, 3)
                .attr('fill', this.opt.bkgMagnetic)
                .data('pcode', this.code)
                .data('posi', posi)
        }
        let rElem: RaphaelElement
        if(this.tRElem['mc']){
            rElem = this.tRElem['mc']
        }
        return rElem
    }
    /**
     * [BUG20180417]  Uncaught TypeError: Cannot read property 'nextSibling' of null
     * 
     */    
    select(): rSu.Node{
        let selMk = false,
            bAttr = this.getBBox(),
            {attr, ps} = bAttr,
            {x, y, width, height} = attr,
            {paper} = this,
            ist:RaphaelElement 
        
        this.removeBox()
        this.isSelEd = true        
        this.tRElem['box'] = paper.rect(x, y, width, height)
            .attr({
                'stroke': '#0033FF',
                'stroke-width': '0.8'
            })        
        for(var key in ps){
            let {x, y} = ps[key]
            this.tRElem['__p' + key] = paper.circle(x, y, 2)
                .attr('fill', '#000000')
                .data('pcode', this.code)
                .data('posi', key)
            this.onCreateBoxPnt(this.tRElem['__p' + key])            
        }
        return <rSu.Node>this
    }
    /**
     * 
     */
    removeBox(){
        if(!this.tRElem){
            this.tRElem = {}
        }
        // 
        if(this.tRElem.box){
            this.tRElem.box.remove()
            delete this.tRElem.box
        }
        for(var key in this.tRElem){
            if(key.indexOf('__p') > -1){
                this.tRElem[key].remove()
                delete this.tRElem[key]
            }
        }
        this.isSelEd = false
        return this
    }
    /**
     * 
     * @param {number} rate  0.05 [0-1]
     */
    zoomOut(rate?: number): rSu.Node{
        rate = rate? rate: 0.05
        var {c, opt} = this
        opt.w = opt.w * (1 + rate)
        opt.h = opt.h * (1 + rate)
        this.updAttr({
            w: opt.w,
            h: opt.h
        })
        this.select()
        return <rSu.Node>this
    }
    /**
     * 
     * @param {number} rate  0.05 [0-1]
     */
    zoomIn(rate?: number): rSu.Node{
        rate = rate? rate: 0.05
        var {c, opt} = this
        opt.w = opt.w * (1 - rate)
        opt.h = opt.h * (1 - rate)
        this.updAttr({
            w: opt.w,
            h: opt.h
        })
        this.select()
        return <rSu.Node>this
    }
    /**
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move(type?: string, rate?: number): rSu.Node{
        rate = rate? rate: 0.05
        var {opt} = this,
            uOpt: rSu.NodeOpt
        type = type? type.toUpperCase() : type
        switch(type){
            case 'T':
                uOpt = {cy: opt.cy * (1 - rate)}
                break
            case 'B':
                uOpt = {cy: opt.cy * (1 + rate)}
                break
            case 'L':
                uOpt = {cx: opt.cx * (1 - rate)}
                break
            case 'R':
                uOpt = {cx: opt.cx * (1 + rate)}
                break
        }
        if(uOpt){
            this.updAttr(uOpt)
            this.select()
        }
        return <rSu.Node>this
    }
    /**
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move2T(rate?: number): rSu.Node{
        this.move('T', rate)
        return <rSu.Node>this
    }
    /**
     * 
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move2B(rate?: number): rSu.Node{
        this.move('B', rate)
        return <rSu.Node>this
    }
    /**
     * 
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move2L(rate?: number): rSu.Node{
        this.move('L', rate)
        return <rSu.Node>this
    }
    /**
     * 
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move2R(rate?: number): rSu.Node{
        this.move('R', rate)
        return <rSu.Node>this
    }
    /**
     * 
     * @param {string} type  
     */
    background(type?: string|string[]): rSu.Node{
        if('object' == typeof type){
            Util.each(type, (i: number, t: string) => {
                this.background(t)
            })
            return
        }
        if(type){
            type = type.toLowerCase()
        }
        switch(type){
            case 'magn':        // 
                this.c.attr('fill', this.opt.bkgMagnetic)
                break
            case 'text':
                if(!this.opt.bkgTxt){
                    this.opt.bkgTxt = '#000000'
                }
                if(this.label){
                    this.label.attr('fill', this.opt.bkgTxt)
                }
                break
            case 'node':
            default:
                this.c.attr('fill', this.opt.bkg)
        }
        return <rSu.Node>this
    }
    /**
     * 
     * @param key 
     */
    clearTmpElem(key?: string|Array<string>): rSu.Node{
        if(key){
            let tArr: Array<string> = []
            if('object' == typeof key){
                tArr = key
            }else{
                tArr = [key]
            }
            Util.each(this.tRElem, (k: string, elem: RaphaelElement) => {
                if($.inArray(k, tArr) > -1){
                    elem.remove()
                    delete this.tRElem[k]
                }
            })            
        }else{
            Util.each(this.tRElem, (k: string, elem: RaphaelElement) => {
                elem.remove()
                delete this.tRElem[k]
            })  
        }
        return <rSu.Node>this
    }
    /**
     *  [] 
     */
    onCreateBoxPnt(rElem: RaphaelElement){}
    /**
     * 
     * @memberof NodeAbstract
     */
    onSize(){}
}


